<template>

    <div class="mb-5">
        <div class="justifyClass mt-3">get API Example</div>
        <div id="settingTable" class="mt-5 ml-5 mr-5">
          <b-table
            id="selectableTable"
            ref="selectableTable"
            :select-mode="selectModeTable"
            :fields="fields"
            :items="getcompanyData"
            :striped="true"
            :bordered="true"
            :borderless="true"
            head-variant="dark"
            fixed
            sticky-header
            show-empty
          >
            <template v-slot:cell(Year)="item">{{ item.value }}</template>
            <template v-slot:cell(EmployeeNumber)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(Capital)="item">{{ item.value }}</template>
            <template v-slot:cell(Revenue1)="item">{{ item.value }}</template>
            <template v-slot:cell(Floorspace)="item">{{ item.value }}</template>
            <template v-slot:cell(NumberOfStores)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(IndustryType)="item">{{
              item.value
            }}</template>
            <template v-slot:table-busy>
              <div class="text-center text-danger my-2">
                <b-spinner class="align-middle"></b-spinner>
              </div>
            </template>
                   <template v-slot:empty>
        <div class="text-center">
         No Data Available
        </div>
      </template>
          </b-table>
 
    </div>
        <div class="justifyClass alignTop mt-5 mb-5">Post API Example</div>
        <div id="settingTable" class="mt-5 ml-5 mr-5">
          <b-table
            id="selectableTable"
            ref="selectableTable"
            :select-mode="selectModeTable"
            :fields="fieldss"
            :items="companyData"
            :striped="true"
            :bordered="true"
            :borderless="true"
            head-variant="dark"
            fixed
            sticky-header
            show-empty
          >
            <template v-slot:cell(Year)="item">{{ item.value }}</template>
            <template v-slot:cell(EmployeeNumber)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(Capital)="item">{{ item.value }}</template>
            <template v-slot:cell(Revenue1)="item">{{ item.value }}</template>
            <template v-slot:cell(Floorspace)="item">{{ item.value }}</template>
            <template v-slot:cell(NumberOfStores)="item">{{
              item.value
            }}</template>
            <template v-slot:cell(IndustryType)="item">{{
              item.value
            }}</template>
            <template v-slot:table-busy>
              <div class="text-center text-danger my-2">
                <b-spinner class="align-middle"></b-spinner>
              </div>
            </template>
             <template v-slot:empty>
        <div class="text-center">
         No Data Available
        </div>
      </template>
          </b-table>
 
    </div>
    </div>
</template>
<script>
export default {
    data() {
    return {
        companyData:[],
        companysizeInfo:[],
       selectModeTable: "single",
        getcompanyData:[],

    }
    },
      computed: {
     fieldss() {
      return [
        {
          key: "Year",
          label: "Year",
          class: "text-center normal-width",
        },
        {
          key: "EmployeeNumber",
          label: "EmployeeNumber",
          class: "text-center normal-width",
        },
        {
          key: "Capital",
          label: "Capital1",
          class: "text-center normal-width",
        },
        {
          key: "Revenue",
          label: "Revenue1",
          class: "text-center normal-width",
        },

        {
          key: "Floorspace",
          label: "FloorSpace2",
          class: "text-center normal-width",
        },
        {
          key: "NumberOfStores",
          label: "NumberOfStores",
          class: "text-center normal-width",
        },
        {
          key: "IndustryType",
          label: "Industry",
          class: "text-center normal-width",
        },
      ];
     },
     fields() {
      return [
        {
          key: "Year",
          label: "Year",
          class: "text-center normal-width",
        },
        {
          key: "EmployeeNumber",
          label: "EmployeeNumber",
          class: "text-center normal-width",
        },
        {
          key: "Capital",
          label: "Capital1",
          class: "text-center normal-width",
        },
        {
          key: "Revenue",
          label: "Revenue1",
          class: "text-center normal-width",
        },

        {
          key: "Floorspace",
          label: "FloorSpace2",
          class: "text-center normal-width",
        },
        {
          key: "NumberOfStores",
          label: "NumberOfStores",
          class: "text-center normal-width",
        },
        {
          key: "IndustryType",
          label: "Industry",
          class: "text-center normal-width",
        },
      ];
     }
    },
    mounted() {
    this.companyTableData();
    this.getApiMethod();
  },
    methods: {
    getApiMethod()
    {
         fetch(
        `https://mng.kanbei.develop.groony.jp/api/master/readall/m_client_company_size_info`
      )
        .then((response) => response.json())
        .then((data) => {
               
      let ClientCompanyData = data.data;
      if (ClientCompanyData.length > 0) {
        let responseData = data.data;
        responseData.forEach((item) => {
          if (item.company_size == 1) {
            //大、中、小
            this.companytype ="small";
          } else if (item.company_size == 2) {
            this.companytype = "Medium";
          } else if (item.company_size == 3) {
            this.companytype = "Big";
          } else {
            this.companytype = "";
          }
          let revenue;
          let FloorSpace;
          if (item.revenue == null && item.floor_space == null) {
            revenue = null;
            FloorSpace = null;
          } else if (item.revenue != null && item.floor_space == null) {
            revenue =
              item.revenue;
            FloorSpace = null;
          } else if (item.revenue == null && item.floor_space != null) {
            revenue = null;
            FloorSpace = item.floor_space;
          } else {
            revenue =
             item.revenue;

            FloorSpace = item.floor_space;
          }

          let tableinfo = {
            Year: item.info_year,
            EmployeeNumber: item.employee_number,
            Capital:
            item.capital,
            Revenue: revenue,
            Floorspace: FloorSpace,
            NumberOfStores: item.number_of_stores,
            IndustryType: this.companytype,
          };
          this.getcompanyData.push(tableinfo);
        });


    }
        });   
    },
     async companyTableData() {
         await fetch(
        `https://ap.kanbei.co.jp/api/master/readdata/m_client_company_size_info`,
        {
          // Adding method type
          method: "POST",

          // Adding body or contents to send
          body: JSON.stringify({
            fieldlist: ["*"],
            client_no: 29,
          }),

          // Adding headers to the request
          headers: {
            "Content-type": "application/json;",
          },
        }
      )
        // Converting to JSON
        .then((response) => response.json())

        // Displaying results to console
        .then((data) => {
          console.log(data.data);
               
      let ClientCompanyData = data.data;
      if (ClientCompanyData.length > 0) {
        let responseData = data.data;
        responseData.forEach((item) => {
          if (item.company_size == 1) {
            //大、中、小
            this.companytype ="small";
          } else if (item.company_size == 2) {
            this.companytype = "Medium";
          } else if (item.company_size == 3) {
            this.companytype = "Big";
          } else {
            this.companytype = "";
          }
          let revenue;
          let FloorSpace;
          if (item.revenue == null && item.floor_space == null) {
            revenue = null;
            FloorSpace = null;
          } else if (item.revenue != null && item.floor_space == null) {
            revenue =
              item.revenue;
            FloorSpace = null;
          } else if (item.revenue == null && item.floor_space != null) {
            revenue = null;
            FloorSpace = item.floor_space;
          } else {
            revenue =
             item.revenue;

            FloorSpace = item.floor_space;
          }

          let tableinfo = {
            Year: item.info_year,
            EmployeeNumber: item.employee_number,
            Capital:
            item.capital,
            Revenue: revenue,
            Floorspace: FloorSpace,
            NumberOfStores: item.number_of_stores,
            IndustryType: this.companytype,
          };
          this.companyData.push(tableinfo);
        });


    }
    });
    },
},
};
</script>
<style>
.justifyClass
{
    display: flex !important;
    justify-content: center !important;
}
.table-height {
  min-height: 300px;
}
.b-table-sticky-header {
    font-size: 0.8rem!important;
}
.b-table-sticky-header {
    overflow-y: auto;
    max-height: 300px;
}
.b-table-sticky-header, .table-responsive, [class*=table-responsive-] {
    margin-bottom: 1rem;
}
.table td {
  word-wrap: break-word;
  text-align: center;
}
.normal-width {
  width: 80px !important;
}
  #settingTable ::-webkit-scrollbar {
    width: 4px;
    height: 4px;
    background: #d3d3d3 0% 0% no-repeat padding-box;
    border-radius: 4px;
    opacity: 1;
  }
  #settingTable ::-webkit-scrollbar-thumb {
    background: #033170;
    height: 5px !important;
  }
  #settingTable ::-webkit-scrollbar-corner {
    background: #033170 0% 0% no-repeat padding-box !important;
  }
  #settingTable * {
    scrollbar-width: thin;
    scrollbar-color: #033170 #cfd8dc;
  }

@media only screen and (max-width: 773px) {
  #settingTable ::-webkit-scrollbar {
    width: 3px;
    height: 3px;
    background: #d3d3d3 0% 0% no-repeat padding-box;
    border-radius: 4px;
    opacity: 1;
  }

  #settingTable ::-webkit-scrollbar-corner {
    background: #033170 0% 0% no-repeat padding-box !important;
  }
  .alignTop
{
  margin-top: 150px !important;
}
.login{  
      
        width: 300px;  
        overflow: hidden;  
        margin: auto;  
        /* margin: 200px 0 0 0px !important;   */
        padding: 80px;  
        background-color: #fff;
       border: none;
       border-radius: 8px;
        border-radius: 15px ;   
        box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);   
}

}
</style>
